@font-face {
  font-family: Lato-regular;
  src: url("./fonts/Lato-Regular.ttf");
  font-weight: normal;
}
@font-face {
  font-family: Lato-Light;
  src: url("./fonts/Lato-Light.ttf");
  font-weight: normal;
}
@font-face {
  font-family: Lato-Bold;
  src: url("./fonts/Lato-Bold.ttf");
  font-weight: normal;
}
@font-face {
  font-family: Lato-Black;
  src: url("./fonts/Lato-Black.ttf");
  font-weight: normal;
}
@font-face {
  font-family: pfd;
  src: url("./fonts/pfdintextcomppro-medium.ttf.ttf");
  font-weight: normal;
}
@font-face {
  font-family: "svgfont";
  src: url("fonts/svgfont.eot");
  src: url("fonts/svgfont.eot?#iefix") format('eot'), url("fonts/svgfont.woff") format('woff'), url("fonts/svgfont.woff2") format('woff2'), url("fonts/svgfont.ttf") format('truetype');
  font-weight: normal;
  font-style: normal;
}
icon,
.icon,
[class^="icon-"],
[class*=" icon-"] {
  font-family: "svgfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon,
[class^="icon-"],
[class*=" icon-"] {
  display: inline-block;
}
.icon-sepa:before {
  content: "\EA01";
}
.icon-skrill:before {
  content: "\EA02";
}
.icon-01:before {
  content: "\EA03";
}
.icon-02:before {
  content: "\EA04";
}
.icon-03:before {
  content: "\EA05";
}
.icon-04:before {
  content: "\EA06";
}
.icon-05:before {
  content: "\EA07";
}
.icon-06:before {
  content: "\EA08";
}
.icon-07:before {
  content: "\EA09";
}
.icon-calendar:before {
  content: "\EA0A";
}
.icon-client:before {
  content: "\EA0B";
}
.icon-dollar:before {
  content: "\EA0C";
}
.icon-instagram:before {
  content: "\EA0D";
}
.icon-login:before {
  content: "\EA0E";
}
.icon-vk:before {
  content: "\EA0F";
}
.icon-youtube:before {
  content: "\EA10";
}
body {
  font-family: Lato-Regular;
  font-size: 12px;
  line-height: 1.4;
}
.out {
  color: #008000;
  font-size: 10px;
  font-size: 0.625rem;
  line-height: 15;
  line-height: 0.9375rem;
}
.out:after {
  content: '';
  display: table;
  width: 0;
  height: 0;
  visibility: hidden;
}
.out:after {
  clear: both;
}
@media (min-width: 640px) {
  .out {
    color: #f00;
  }
}
@media (min-width: 800px) {
  .out {
    color: #00f;
    font-size: 16px;
    font-size: 1rem;
    line-height: 24;
    line-height: 1.5rem;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 1200px) {
  .out {
    color: #ffa500;
  }
}
.left {
  float: left;
}
